<template>
    <div class="back-brown text-black" id="navbar">
        <router-link to="/" class="action back-green">首頁</router-link>
        <router-link to="/theclass">觀看課程</router-link>
        <router-link to="/about">關於Ivan</router-link>
        <a onclick="document.getElementById('id01').style.display='block'">會員登入</a>
    </div>
</template>

<script>
export default {
    name: 'navbar'
}
</script>

<style scoped>
#navbar {
    z-index: 10;
    overflow: hidden;
    flex-wrap: wrap;
    justify-content: center;
    display: flex;
}

#navbar a {
    float: left;
    display: block;
    color: #f2f2f2;
    text-align: center;
    padding: 14px 16px;
    text-decoration: none;
    font-size: 17px;
}

#navbar a:hover {
    background-color: #ddd;
    color: black;
}

.action {
    color: black;
}

.content {
    padding: 16px;
}

.sticky {
    position: fixed;
    top: 0;
    width: 100%;

}

.sticky + .content {
    padding-top: 60px;
}
</style>


