<template>
    <div id="allclass">
        <div class="row" style="height: auto;">
            <div v-for="item in classInfo" class="column col-4" id="cla">
                <div class="card">
                    <img v-bind:src="item.imgurl">
                    <h2> {{ item.name }} </h2>
                    <!-- 製造星星 -->
                    <img v-for="st in item.star" src="~@/assets/star.png" style="height: 20px; width: auto;">
                    <img v-for="st in item.nostar" src="~@/assets/nostar.png" style="height: 20px; width: auto;">
                    <p class="title">{{ item.content }}則評價</p>
                    <p>課程共{{ item.time }}分鐘</p>
                    <p><button class="back-blue">NT {{ item.price }} </button></p>
                </div>
            </div>
        </div>
    </div>

</template>

<script>
var classname = ['爵士吉他入門訓練班', '【吉他伴奏大全】', '吉他編曲超訓練：豐富精緻的彈奏設計', '吉他從零開始-Fingerstyle（進階）','十堂課輕鬆學會木吉他自彈自唱','吉他從零開始 - 初級篇','和Ivan一起學吉他','流行歌曲製作秘訣：Cubase 8 編曲軟體應','音樂創作原來這麼簡單！']
var array = []

// 製造亂數
function ramNum(min,max){
    return Math.floor(Math.random()*(max-min+1)+min);
}

// 生成課程資訊
for (var i = 0; i < classname.length; i++){
    var forstar = ramNum(3,5);
    array.push({
        name: classname[i],
        star: forstar,
        nostar: 5-forstar,
        time: ramNum(250,790),
        content: ramNum(30,500),
        price: ramNum(3200,15500),
        imgurl: require('@/assets/square'+i+'.jpg') 
    });
};
        
export default {
    name: 'theclass',
    data() {return {
      classInfo: array
    };
  }
}
</script>

<style scoped>
</style>


