<template>
    <div id="id01" class="modal">
        <form class="modal-content animate back-white" action="" method="post">
            <div class="imgcontainer">
                <span onclick="document.getElementById('id01').style.display='none'" class="close">&times;</span>
                <img src="~@/assets/guitar-pick.png" alt="Avatar" class="avatar">
            </div>

            <div class="container">
                <label for="uname"><b>帳號：</b></label>
                <input type="text" placeholder="請輸入您的帳號" name="uname" required>

                <label for="psw"><b>密碼：</b></label>
                <input type="password" placeholder="請輸入您的密碼" name="psw" required>

                <button class="back-green" type="submit">登入</button>
                <label>
                    <input type="checkbox" checked="checked" name="remember"> 記住帳號
                </label>
            </div>

            <div class="container" style="background-color:#f1f1f1">
                <button type="button" onclick="document.getElementById('id01').style.display='none'"
                    class="cancelbtn back-blue">取消</button>
                <span class="psw">忘記 <a href="#">密碼？</a></span>
            </div>
        </form>
    </div>
</template>

<script>
export default {
    name: 'login'
}
</script>

<style scoped>
.imgcontainer {
    text-align: center;
    margin: 24px 0 12px 0;
    position: relative;
}

img.avatar {
    width: 40%;
    border-radius: 50%;
}

.container {
    padding: 16px;
}

span.psw {
    float: right;
    padding-top: 16px;
}

/* The Modal (background) */
.modal {
    display: none;
    position: fixed;
    z-index: 1;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    overflow: auto;
    background-color: rgb(0, 0, 0);
    background-color: rgba(0, 0, 0, 0.4);
    padding-top: 60px;
}

/* Modal Content/Box */
.modal-content {
    background-color: #fefefe;
    margin: 5% auto 15% auto;
    border: 1px solid #888;
    width: 30%;
}

/* The Close Button (x) */
.close {
    position: absolute;
    right: 25px;
    top: 0;
    color: #000;
    font-size: 35px;
    font-weight: bold;
}

.close:hover,
.close:focus {
    color: red;
    cursor: pointer;
}

/* Add Zoom Animation */
.animate {
    -webkit-animation: animatezoom 0.6s;
    animation: animatezoom 0.6s
}

@-webkit-keyframes animatezoom {
    from {
        -webkit-transform: scale(0)
    }

    to {
        -webkit-transform: scale(1)
    }
}

@keyframes animatezoom {
    from {
        transform: scale(0)
    }

    to {
        transform: scale(1)
    }
}

/* Change styles for span and cancel button on extra small screens */
@media screen and (max-width: 300px) {
    span.psw {
        display: block;
        float: none;
    }

    .cancelbtn2 {
        width: 100%;
    }
}
</style>


