<template>
    <div class="hero">
        <video autoplay muted loop id="myVideo">
            <source src="~@/assets/Playing.mp4" type="video/mp4">
            您的瀏覽器不支援 HTML5 的影片喔！
        </video>

        <div class="black-filter"></div><!-- 增加濾鏡 -->

        <div class="upbox row">

            <div class="column col-6" style="height: 100%;">
                <img id="logo" src="~@/assets/logo.png" style="height: 90px;">
                <h2 style="letter-spacing: 5px;">用音樂彈出靈魂的聲音</h2>
            </div>

            <div class="column col-6" style="height: 100%;">
                <form class="opacity-form back-opacity-brown">
                    <h3>馬上報名</h3>
                    <hr>
                    <label for="name"><b>選擇課程</b></label>
                    <select name="subject" style="width: 100%;">
                        <option value="" selected="selected">古典風</option>
                        <option value="" selected="selected">鄉村演奏</option>
                        <option value="" selected="selected">暢談獨奏曲</option>
                    </select>

                    <label for="name"><b>姓名</b></label>
                    <input type="text" placeholder="我們該如何稱呼您" name="name" required>

                    <label for="phone"><b>電話</b></label>
                    <input type="tel" placeholder="請輸入您的電話" pattern="[0-9]{4}-[0-9]{3}-[0-9]{3}" name="phone" required>

                    <label for="email"><b>Email</b></label>
                    <input type="text" placeholder="請輸入您的Email" name="email" required>

                    <label>
                        <input type="checkbox" checked="checked" name="remember" style="margin-bottom:15px"> 收到最新消息
                    </label>

                    <div class="clearfix">
                        <button type="button" class="floatbtnA back-green">聯絡我們</button>
                        <button type="submit" class="floatbtnB back-blue">報名</button>
                    </div>
                </form>


            </div>
        </div>
    </div>
</template>

<script>
export default {
    name: 'hero'
}
</script>

<style scoped>
.hero {
    position: relative;
    color: #f1f1f1;
    height: 500px;
    width: 100%;
}

#myVideo {
    position: absolute;
    right: 0;
    bottom: 0;
    min-width: 100%;
    min-height: 100%;
}

.upbox {
    position: absolute;
    height: 100%;
    width: 100%;
}

.opacity-form {
    width: 60%;
    padding: 20px;
}

hr {
    border: 1px solid #f1f1f1;
    margin-bottom: 25px;
}

.floatbtnA {
    padding: 14px 20px;
}

.floatbtnA,
.floatbtnB {
    float: left;
    width: 50%;
}
</style>


