import Vue from 'vue'
import Router from 'vue-router'

import homepage from '@/components/homepage.vue'
import aboutIvan from '@/components/aboutIvan.vue'
import theclass from '@/components/theclass.vue'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: '首頁',
      component: homepage
    },
    {
      path: '/about',
      name: '關於Ivan',
      component: aboutIvan
    },
    {
      path: '/theclass',
      name: '線上課程',
      component: theclass
    }
  ]
})



